    queryBusiBatchDetailInfo() {
        let params = {
            batchId: this.crossSellingQueryBean.batch_id,//批次编号
            custNo: this.crossSellingQueryBean.cust_no,//客户号
            custName: this.crossSellingQueryBean.cust_name,//客户名称
            recoProdId: this.crossSellingQueryBean.reco_prod_id,//推荐产品编号
            // recoProdName: this.crossSellingQueryBean.reco_prod_name,//推荐产品名称
            principalLinkmanId: this.crossSellingQueryBean.principal_Linkman_Id,//客户主联系人工号
            // principalLinkmanName: this.crossSellingQueryBean.principal_Linkman_Name,//客户主联系人名称
            dtCreatedateStart: this.commfunc.transDateN(this.startDate),//	创建日期起
            dtCreatedateEnd: this.commfunc.transDateN(this.endDate),//	创建日期止
            pushStat: this.crossSellingQueryBean.push_stat, //推送状态
            savBalStart: this.crossSellingQueryBean.sav_Bal_start, //存款余额起
            savBalEnd: this.crossSellingQueryBean.sav_Bal_end, //存款余额止
            lmDeptAvgBalStart: this.crossSellingQueryBean.lm_Dept_Avg_Bal_start, //存款上月日均起
            lmDeptAvgBalEnd: this.crossSellingQueryBean.lm_Dept_Avg_Bal_end, //存款上月日均止
            loanBalStart: this.crossSellingQueryBean.loan_Bal_start, //贷款余额起
            loanBalEnd: this.crossSellingQueryBean.loan_Bal_end, //贷款余额止
            cerdCardBalStart: this.crossSellingQueryBean.cerd_Card_Bal_start, //信用卡余额起
            cerdCardBalEnd: this.crossSellingQueryBean.cerd_Card_Bal_end, //信用卡余额止
            dataSource: this.crossSellingQueryBean.data_source,//数据来源
            rateStart: this.crossSellingQueryBean.rateStart,//概率
            rateEnd: this.crossSellingQueryBean.rateEnd,//概率
            predResult: this.crossSellingQueryBean.predResult,//是否流失
            pageNum: this.crossSellingQueryBean.pageNum,	//页码
            pageSize: this.crossSellingQueryBean.pageSize,	//每页显示记录条数
        }
        this.httpService.queryBusiBatchDetailInfo(params).subscribe(data => {
            console.log('1');
            if (data.returnCode.code == 'success') {
                this.detailTable = data.interfaceModuleList;
                this.total = data.totalNum;
                for (let i = 0; i < this.detailTable.length; i++) {
                    if (this.checkSave.indexOf(this.detailTable[i].custNo + '|' + this.detailTable[i].productNo) !== -1) {
                        this.detailTable[i].checkBox = true;
                    }
                }
                for (let i = 0; i < this.detailTable.length; i++) {
                    if (this.detailTable[i].checkBox == false || this.detailTable[i].checkBox == undefined) {
                        this.checkAll = false;
                        break;
                    } else {
                        this.checkAll = true;
                    }
                }
                // console.log(data)
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    //重置
