import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { CustomCrossSellingQueryComponent } from './custom-cross-selling-query/custom-cross-selling-query.component';
import { CustomCrossSellingListComponent } from './custom-cross-selling-list/custom-cross-selling-list.component';

export const routes: Routes = [
    {
        path: '',

        children: [
            { path: '', redirectTo: 'custom-cross-selling-list', pathMatch: 'full' },
            { path: 'custom-cross-selling-query', component: CustomCrossSellingQueryComponent },
            { path: 'custom-cross-selling-list', component: CustomCrossSellingListComponent }
        ]
    },
]
export const CustomCrossSellingRouting: ModuleWithProviders = RouterModule.forChild(routes);