    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
    }

    ngOnInit() {
        // this.list_Table = [
        //     { custNo: '11' }
        // ]
        this.codeVal();
        this.listOptions = this.code['Source_stat'];//清单状态
        this.dataSourceOptions = this.code['Data_Source'];//数据来源
        this.query();
        this.queryUserInValue = this.orgId;
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    showDataStart() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.startDate = null;
                })
            }
        }
    }
    showDataEnd() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                setTimeout(() => {
                    this.endDate = null;
                })
            }
        }
    }
