    //创建人选择回调
    perInfor(param) {
        this.orgTreeDisplay2 = false;
        this.crossSellingListQueryBean.created_user_id = param[0].tellerId;
        this.crossSellingListQueryBean.created_user_name = param[0].tellerName;
        document.body.style.overflow = 'auto';
      }
      // 选择创建人
      perShow() {
        this.orgTreeDisplay2 = true;
       }
    //查询
    query() {
        this.crossSellingListQueryBean.pageSize = 10;
        this.crossSellingListQueryBean.pageNum = 1;
        this.first = 0;
        this.queryBusiBatchListInfo();
    }
    queryBusiBatchListInfo() {
        let params = {
            batchId: this.crossSellingListQueryBean.batch_id,//批次编号
            dtCreatedateStart: this.commfunc.transDateN(this.startDate),//	创建日期起
            dtCreatedateEnd: this.commfunc.transDateN(this.endDate),//	创建日期止
            dataSource: this.crossSellingListQueryBean.data_source, //数据来源
            createdUserId: this.crossSellingListQueryBean.created_user_id, //创建人工号
            createdUserName: this.crossSellingListQueryBean.created_user_name, //创建人名称
            sourceStat: this.crossSellingListQueryBean.source_stat, //清单状态
            pageNum: this.crossSellingListQueryBean.pageNum,	//页码
            pageSize: this.crossSellingListQueryBean.pageSize,	//每页显示记录条数
        }
        this.httpService.queryBusiBatchListInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list_Table = data.busiBatchListInfo;
                this.total = data.totalNum;
                // console.log(data)
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    //重置
    reset() {
        this.crossSellingListQueryBean = new CrossSellingListQueryBean();
        this.startDate = null;
        this.endDate = null;
    }
    //翻页
    paginate(event) {
        this.crossSellingListQueryBean.pageSize = event.rows * 1;
        //当前页
        this.crossSellingListQueryBean.pageNum = event.page + 1;
        this.first = event.page * this.crossSellingListQueryBean.pageSize;
        this.queryBusiBatchListInfo();
    }
